* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background: #cbdbe7;
  color: #222;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}
.book {
  background: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
}
.book h1 {
  margin-top: 0.5rem;
}
.book h4 {
  color: #617d98;
  font-size: 0.5rem;
  margin-top: 0.25rem;
}
@media screen and (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
  }
  .book {
    background: #fff;
    border-radius: 1rem;
    padding: 1rem 2rem;
    align-items: center;
  }
}
